@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
  .sidebar-icon{
    @apply relative flex items-center justify-center h-12 w-12 my-2  mx-auto shadow-lg
           bg-gray-200 text-yellow-400 hover:bg-yellow-400 hover:text-white
           rounded-3xl hover:rounded-xl transition-all duration-300 ease-linear cursor-pointer
            dark:bg-primary  dark:border-slate-400 dark:hover:bg-yellow-400;
  }
  .sidebar-Tip{
    @apply absolute w-auto p-2 m-2 min-w-max left-14 rounded-md shadow-md text-white
           bg-gray-900 text-xs font-bold transition-all duration-100 scale-0 origin-left;
  }
}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
